/******************************************************************
VARIABLES - COLORS
******************************************************************/

// Theme colors
$color01-dark:              #bda171;
$color01:                   #D2B37F;
$color01-light:             #E8D8BD;


// Gradients
$gradient01:                linear-gradient(0deg, $color01 0%, $color01-light 100%);


// Grayscale
$color-black:               #000000;
$color-white:               #ffffff;

$color-gray-lightest:       #f7f7f7;
$color-gray-lighter:        #efefef;
$color-gray-light:          #d4d4d4; //#d6dee2; // #BEC3C6
$color-gray:                #BEC3C6;
$color-gray-dark:           #ACB2B5;
$color-gray-darker:         #b3b3b3;
$color-gray-darkest:        #666;


// State & alert colors
$color-positive:            #22d29c;
$color-positive-dark:       #10544b;
$color-negative:            #C5014C;
$color-negative-dark:       #8e0037;
$color-warning:             #F6A609;
$color-warning-dark:        #a77410;
$color-special:             #ac4de8;
$color-neutral:             $color01;
$color-disabled:            $color-gray;
$color-placeholder:         $color-gray;


// Opacities
$opacity-sm:                0.75;
$opacity-md:                0.48;
$opacity-lg:                0.25;


// Base element colors
$color-text:                $color-black;
$color-heading:             $color-black;
$color-link:                $color-text;
$color-hover:               rgba($color-link, $opacity-md);
$color-visited:             rgba($color-link, $opacity-md);

$color-selection:           $color-white;
$color-selection-bg:        $color01;