/******************************************************************
EDITOR
Styles for WordPress TinyMCE editor content output.
Always use a container with .editor-content class to output editor content.
******************************************************************/


.editor-content, .block-editor {
	line-height: $line-height-base;
	font-size: 18px;

	> *:first-child {
		margin-top: 0;
	}
	> *:last-child {
		margin-bottom: 0;
	}

	H1, .h1, H2, .h2 {
		font-size: $font-size-h3;

		@include below-mobile {
			font-size: 24px;
			line-height: $line-height-md;
		}
	}

	H3, .h3 {
		font-size: $font-size-h4;
	}

	H4, .h4 {
		font-weight: $font-weight-black;
	}

	H1, H2, H3, H4, H5, H6 {
		color: $color-black;
		margin: 24px 0;
	}

	P {
		margin: 24px 0;
	}

	STRONG {
		font-weight: $font-weight-bold;
	}

	UL, OL {
		margin: 2px 0 24px 0;
		padding-left: 0;
	}

	OL {
		list-style-position: inside;
	}

	UL {
		list-style-type: none;

		LI {
			position: relative;
			padding-left: 24px;

			&:before {
				content: "";
				display: inline-block;
				position: absolute;
				left: 0;
				top: 8px;
				height: 8px;
				width: 8px;
				border-radius: 4px;
				background-color: $color01;
			}
		}
	}

	P + UL, P + OL {
		margin-top: -16px;
	}

	IMG {
		max-width: 100%;
		height: auto;
		margin-bottom: 30px;

		&.alignleft {
			display: inline;
			float: left;
			margin-right: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	
		&.alignright {
			display: inline;
			float: right;
			margin-left: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	}

	BLOCKQUOTE {
		padding: 40px 40px 40px 100px;
		background: $color-gray-lightest;

		> *:first-child {
			margin-top: 0;
		}
		> *:last-child {
			margin-bottom: 0;
		}
	
		&:before {
			content: "";
			display: block;
			position: absolute;
			top: 40px;
			left: 40px;
			width: 40px;
			height: 40px;
			background: url('../../assets/icons/icon-quotes.svg');
			background-repeat: no-repeat;
			background-position: 50% top;
			background-size: contain;
		}

		@include below-mobile-sm {
			padding: 80px 24px 30px 24px;

			&:before {
				left: 30px;
				top: 30px;
			}
		}
	}

    .wp-caption {
		width: 100%;
		max-width: 300px;
		height: auto;
		margin-bottom: 20px;
		line-height: 1.2;

		IMG {
			margin-bottom: 8px;
		}
		&.alignright {
			float: right;
			padding-left: 10px;
		}
		&.alignleft {
			float: left;
			padding-right: 10px;
		}
		&.aligncenter {
			margin: 0 auto;
		}
	}
	.wp-caption-text {
		//font-style: italic;
		color: #aaa;
		margin-top: 0;
		text-decoration: none !important;
	}

}