/******************************************************************
VARIABLES - EFFECTS
******************************************************************/

// Shadows
$shadow01:              0 4px 20px rgba(0, 0, 0, 0.1);
$shadow02:              0 3px 100px rgba($color-black, 0.08);


// Beziers
$bezier01:              cubic-bezier(.65, .01, .4, .99);


// Borders
$border-radius:         10px;
$border-color:          rgba($color-black, 0.1);


// Transitions
$trans-time:            0.24s;
$trans-type:            ease;

$trans-time-sm:         0.16s;
$trans-time-md:         0.48s;
$trans-time-lg:         0.86s;

$transition-default:    all $trans-time $trans-type;