/******************************************************************
BUTTON STYLES
All button styles, starts with a default baseline
******************************************************************/


.button {
	display: inline-block;
	background: $color01-dark;
	padding: 16px 32px;
	min-width: 56px;
	color: $color-text;
	font-size: $font-size-sm;
	font-weight: $font-weight-bold;
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
	font-style: italic;
	//white-space: nowrap;
	transition: $transition-default;
	cursor: pointer;

	&:hover, &:focus {
		color: $color-text;
		background: $color01-light;
		transition: $transition-default;
	}

	&__text {
		display: inline-block;
	}

	&__loader {
		display: none;

		position: absolute;
		right: -40px;
		top: calc(50% - 12px);
		width: 24px;
		height: 24px;
		border-radius: 50%;
		border: 4px solid $color01-light;
		border-top-color: $color01-dark;
		animation: buttonLoader 1s $bezier01 infinite;
	}

	@include below-mobile-sm {
		width: 100%;
		max-width: 100%;
		min-width: 200px;
		text-align: center;
	}


	// Main variations
	&--skewed {
		transform: skew(-11deg);

		.button__text {
			transform: skew(11deg);
		}
	}

	&--white {
		background: rgba($color-white, 1);

		&:hover, &:focus {
			background: rgba($color-white, $opacity-lg);
		}
	}

	&--positive {
		background: rgba($color-positive, 1);
		color: $color-white;

		&:hover, &:focus {
			color: $color-white;
			background: rgba($color-positive, $opacity-lg);
		}
	}

	&--bordered {
		color: $color-text;
		border: 1px solid $color-gray;
		background: $color-white;
		text-transform: none;

		&:hover, &:focus {
			border: 1px solid $color01-light;
		}
	}

	&--thick-text {
		font-weight: $font-weight-black;
		text-transform: none;
		font-size: $font-size-base;
	}

	&--place-order {
		padding: 24px 48px;
	}

	// Size variations
	&--size-lg {
		padding: 24px 32px;
		min-height: 72px;

		.icon {
			top: calc(50% - 11px);
		}
	}

	&--size-sm {
		padding: 8px 16px;
		min-height: 40px;
		font-size: $font-size-xs;
	}

	// Misc variations
	&--nowrap {
		white-space: nowrap;
	}

	// Icon variations
	&--icon-left, &--icon-right {
		position: relative;

		.icon {
			width: 18px;
			height: 18px;
			position: absolute;
			top: calc(50% - 9px);
		}
	}
	&--icon-left {
		padding-left: 64px;

		.icon {
			left: 30px;
		}

		&:hover {
			.icon {
				animation-name: buttonIconBounceLeft;
				animation-duration: $trans-time-md;
				animation-direction: forwards;
			}
		}
	}
	&--icon-right {
		padding-right: 64px;

		.icon {
			right: 30px;
		}

		&:hover {
			.icon {
				animation-name: buttonIconBounceRight;
				animation-duration: $trans-time-md;
				animation-direction: forwards;
			}
		}
	}
	&--icon-only {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		padding: 0;
		.icon {
			width: 16px;
			height: 16px;
			margin-bottom: 1px;
		}
		font-size: 0;
	}
	&--icon-mobile-only {
		.icon {
			display: none;
		}

		@include below-mobile {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 56px;
			height: 56px;
			min-width: auto;
			padding: 0;

			.icon {
				display: block;
				width: 16px;
				height: 16px;
				margin-bottom: 1px;
			}
			font-size: 0;
		}
	}
	&.button--size-lg {
		.icon {
			top: calc(50% - 10px); // this is here to overwrite icon placement for lg button
		}
	}
	&.button--complete {
		padding-left: 70px;

		.icon {
			height: 24px;
			width: 24px;
			top: calc(50% - 12px);
		}

		&:hover {
			.icon {
				animation: none;
			}
		}
	}

	// States
	&.is-disabled {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: default;
	}
	&.is-loading {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: wait;

		.button__loader {
			display: block;
		}
	}
}

.more-button {
	display: inline-block;
	position: relative;
	padding: 20px 0 20px 28px;
	font-size: $font-size-md;
	font-style: italic;
	font-weight: $font-weight-bold;
	text-transform: uppercase;
	line-height: 1;
	transition: $transition-default;

	&:before {
		content: "";
		position: absolute;
        left: 0;
        top: 0;
        height: 64px;
        width: 64px;
        border-radius: 32px;
        background: $color01;
		z-index: -1;
	}

	&__text {
		display: block;
		position: relative;
		padding: 0 44px 0 0;
		transition: $transition-default;
	}

	.icon {
		position: absolute;
		right: 0;
		top: calc(50% - 11px);
		width: 22px;
		height: 22px;
	}

	&:hover {
		opacity: $opacity-md;
		transition: $transition-default;

		.more-button__text {
			color: $color-text;
			transform: translateX(16px);
			transition: $transition-default;
		}

		.icon {
			animation-name: buttonIconBounceRight;
            animation-duration: $trans-time-md;
            animation-direction: forwards;
		}
	}

	@include below-mobile {
		font-size: $font-size-base;
	}

	@include below-mobile-sm {
		padding: 16px 0 16px 22px;
		font-size: $font-size-sm;

		&:before {
			height: 48px;
			width: 48px;
		}

		&__text {
			padding-right: 32px;
		}

		.icon {
			top: calc(50% - 9px);
			width: 18px;
			height: 18px;
		}
	}
}

.loadmore-button {
	position: relative;
	padding: 8px;
	color: $color-text;
	text-align: center;
	font-size: $font-size-h4;
	font-weight: $font-weight-bold;
	text-transform: uppercase;
	font-style: italic;
	white-space: nowrap;
	border-bottom: 2px solid rgba($color-text, 1);
	overflow: hidden;
	transition: $transition-default;

	&:hover {
		color: $color-text;
		opacity: $opacity-md;
		transition: $transition-default;
	}

	&.is-active {
		opacity: $opacity-md;
		transition: $transition-default;
	}
}

.play-button {
	position: relative;
	width: 96px;
	height: 96px;
	border-radius: 96px;
	background: $color01;
	color: $color-white;
	box-shadow: $shadow01;
	transition: $transition-default;

	&__icon {
		margin-top: 2px;
		margin-left: 7px;
		height: 36px;
		width: 36px;
	}

	&__tooltip {
		position: absolute;
		top: -80px;
		left: calc(50% - 160px);
		width: 320px;
		opacity: 0;
		transform: translateY(8px);
		pointer-events: none;
		transition: $transition-default;
	}

	&:hover {
		background: $color01-light;
		color: $color-white;
		transition: $transition-default;

		.play-button__tooltip {
			opacity: 1;
			transform: translateY(0);
			transition: $transition-default;
		}
	}

	@include below-mobile {
		width: 56px;
		height: 56px;

		&__icon {
			height: 24px;
			width: 24px;
		}

		&__tooltip {
			display: none;
		}
	}
}