/******************************************************************
VARIABLES - TYPOGRAPHY
******************************************************************/

// Fonts
$font-family-main:             Montserrat, Helvetica, Arial, sans-serif;
$font-family-headings:         Montserrat, Helvetica, Arial, sans-serif;
$font-family-accents:          Montserrat, Helvetica, Arial, sans-serif;


// Sizes
$font-size-base:        20px;

$font-size-xxl:         54px;
$font-size-xl:          48px;
$font-size-lg:          36px;
$font-size-md:          24px;
$font-size-sm:          18px;
$font-size-xs:          16px;

$font-size-h1:          $font-size-xxl;
$font-size-h2:          $font-size-xl;
$font-size-h3:          $font-size-lg;
$font-size-h4:          $font-size-md;
$font-size-h5:          $font-size-base;


// Line heights
$line-height-base:      1.6;
$line-height-sm:        1.12;
$line-height-md:        1.24;
$line-height-lg:        2.0;


// Letter spacing
$letter-spacing-base:   normal;


// Weights
$font-weight-light:     300;
$font-weight-normal:    400;
$font-weight-medium:    500;
$font-weight-semibold:  600;
$font-weight-bold:      700;
$font-weight-black:     800;

$font-weight-base:      $font-weight-normal;